.calculator-container {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    width: 50%;
    /* height: auto; */
    height: 10%;
    margin-bottom: 1%;
}

.gx-calculator {
    top: 10%;
    left: 10%;
    position: relative;
    width: 80%;
    /* height: 77%; */
}

.LP-amount {
    text-align: center;
    justify-content: center;
    font-family: Monospace ;
    background-color: #433848;
    color: #CEB330;
    position: relative;
    font-size: 62px;
    width: 25%;
    height: 95px;
    top: -247px;
    left: 41%;
    /* right: 35%; */
}

.lp-gauge {
    position: relative;
    right: 60.5%;
    top: 40px;
    /* bottom: 25px; */
    height: 190px;
}

.lp-controls {
    position: relative;
    top: 5%;
    right: 55px;
    width: 10%;
    height: 300px;
}

.lp-text {
    text-align: center;
    width: 93%;
    height: 10%;
}
.lp-add {
    background-color: #2377dc;
    width: 100%;
    height: 25%;
}

.lp-subtract {
    background-color: #e42c30;
    width: 100%;
    height: 25%;
}

.lp-reset {
    color: white;
    background-color: darkgreen;
    width: 100%;
}

.skill {
    width: 100%;
}

.skill-use {
    width: 100%;
}